<template>
  <div>
    <div class="poptips" :class="{'poptips-show': showTips}">
      <div class="container">
        <i class="icon poptips-icon"></i>
        <span>提示：本次矿工费由币威平台承担</span>
      </div>
    </div>

    <div class="container">
      <p><br></p>
      <p><br></p>
      <p><br></p>
      <p><br></p>
      <div class="row">
        <div class="col-md-6 col-md-offset-3">
          <form class="form-horizontal">
            <div class="form-group no-corner">
              <label class="control-label col-md-4">{{symbol}}钱包地址:</label>
              <div class="col-md-8">
                <input type="text" class="form-control" v-model="walletAddr" placeholder="请输入相应币种的钱包地址">
              </div>
            </div>
            <div class="form-group no-corner">
              <label class="control-label col-md-4">输入手机号:</label>
              <div class="col-md-8">
                <div class="input-group">
                  <input disabled type="text" class="form-control" :value="userInfo.mobile">
                  <span class="input-group-btn">
                    <button :disabled="disableSms" type="button" class="btn btn-gray-light" @click="getVcode">
                      <span v-if="disableSms"> 请等待({{ countDown }}s)</span>
                      <span v-else>获取验证码</span>
                    </button>
                  </span>
                </div>
              </div>
            </div>
            <div class="form-group no-corner">
              <label class="control-label col-md-4">输入手机验证码:</label>
              <div class="col-md-8">
                <input type="text" class="form-control" v-model="vcode">
              </div>
            </div>
            <p><br></p>
            <p><br></p>
            <div class="form-group">
              <div class="col-md-8 col-md-offset-4">
                <button type="button" class="btn btn-primary" @click="onSubmit" style="padding:6px 20px;">提交</button>
                <button type="button" class="btn btn-gray" @click="onCancel" style="margin-left:20px;padding:6px 20px;color:#fff;">取消</button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  computed: {
    ...mapState({
      id: state => state.route.params.id,
      protocol: state => state.route.params.protocol,
      symbol: state => state.route.query.symbol,
      userInfo: state => state.userInfo
    })
  },
  data () {
    return {
      showTips: true,
      walletAddr: '',
      vcode: '',
      timerId: '',
      disableSms: false,
      countDown: 60
    }
  },
  methods: {
    getVcode () {
      this.disableSms = true
      this.timerId = setInterval(() => {
        if (this.countDown <= 1) {
          clearInterval(this.timerId)
          this.disableSms = false
          this.timerId = ''
          this.countDown = 60
        } else {
          this.countDown--
        }
      }, 1000)
      this.$http.post('/api/getVcode', {
        mobile: this.userInfo.mobile
      }).then(res => {
        if (res.data.errcode === 0) {
        } else {
          alert(res.data.errmsg)
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    onSubmit () {
      // 钱包地址正则校验
      var reg = ''
      if (this.protocol === '1') {
        reg = /^0x[0-9a-f]{40}$/i
      } else if (this.protocol === '2' || this.protocol === '3' || this.protocol === '4' || this.protocol === '5') {
        reg = /^[0-9a-z]{30,40}$/i
      } else {
        alert('未知错误')
        return false
      }
      if (!reg.test(this.walletAddr)) {
        return alert('请填写正确的收币钱包地址')
      }
      // 创建钱包
      this.$http.post('/api/addUserWallet', {
        mobile: this.userInfo.mobile,
        vcode: this.vcode,
        tokenProtocol: this.protocol,
        walletAddr: this.walletAddr
      }).then(res => {
        if (res.data.errcode === 0) {
          // 提币
          console.log('withdraw')
          this.$http.post('/api/withdraw', {
            assetId: this.$route.params.id
          }).then(res => {
            if (res.data.errcode === 0) {
              this.$message({
                type: 'success',
                message: '提交成功!'
              })
              this.$router.push('/wallet')
            } else {
              alert(res.data.errmsg)
            }
          })
        } else {
          alert(res.data.errmsg)
        }
      })
    },
    onCancel () {
      this.$router.push('/wallet')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/styles/variables';

.poptips {
  position: relative;
  padding: 10px 0;
  margin-top: -40px;
  font-size: 14px;
  text-align: center;
  color: $gray-darker;
  background-color: #fce3bf;

  opacity: 0;
  visibility: hidden;
  transition: all .35s ease-in;
  transform: translateY(-40px);

  &.poptips-show {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}
.poptips-icon {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  margin-top: -3px;
  margin-right: 10px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 100%;
}
</style>



